
import './App.css';
import React from 'react';
import Game from './Game.js'
import 'antd/dist/antd.css'
import { Button, Result } from 'antd';

function App() {
  const questions = [
    {
      title: 'Чего вам сейчас больше хочется в работе?',
      variants: ['Активно внедрять новые технологии, фокусироваться на глобальных задачах.',
      'Ломать стереотипы, привычные бизнес-процессы и использовать, пусть и сложные, зато нестандартные творческие решения.',
      'Не изобретать велосипед, а доводить до совершенства то, что есть.'],
      correct: 0,
    },
    {
      title: 'Вдохновляют ли вас digital-инструменты?',
      variants: ['Определенно — ведь за ними будущее. Мобильные платформы, виртуальная реальность — всё это ужасно интересно!',
      'Да, но я пока побаиваюсь применять их в работе. Новые технологии хороши тогда, когда все понимают ценность и необходимость их внедрения!',
      'Я предпочитаю проверенные форматы, тем более что они реально работают и зачастую дешевле обходятся.'],
      correct: 1,
    },
    {
      title: 'Если отбросить все рутинные задачи и нюансы, каким направлением вы бы занялись сейчас?',
      variants: [
        ' Выстраиванием эффективной системы привлечения персонала. Наступило время нестандартных подходов!',
        'Обучением, развитием компетенций сотрудников. Корпоративные летние школы, обучающие мастер-классы, образовательные академии и не только.',
        'Развитием корпоративной культуры. А как иначе работать с вовлеченностью сотрудников в период организационных изменений в компании?',
      ],
      correct: 2,
    },
    {
      title: 'Какая тактика поведения вам ближе, когда речь идет о внедрении новых инструментов в работу?',
      variants: [
        'Все стоят, а я бегу вперед.',
        'Все побежали, и я побежал.',
        'Все побежали, а я постою, посмотрю, что получится у тех, кто побежал.',
      ],
      correct: 2,
    },
    {
      title: 'Согласны ли вы с тем, что HR-специалист сегодня должен осваивать навыки маркетолога и ИТ-специалиста      ',
      variants: [
        ' Да, а еще я чтец, игрец и, когда нужно, продавец идей.',
        'Маркетолога — еще куда ни шло, а ИТ — это уже слишком!',
        'Нет! Если что, всегда можно призвать коллег из маркетинга и ИТ на помощь.',
      ],
      correct: 2,
    },
    {
      title: 'С какой командой вам интереснее и легче работать над HR-проектом?',
      variants: [
        'Собираю всех, кто может и готов думать — ИТ, маркетинг, продажи. Отрабатываем кросс-отраслевой подход.',
        'HR-департамент, конечно! Команда единомышленников.',
        'Я сам себе режиссер, но при необходимости можно и расширить состав.',
      ],
      correct: 2,
    },
    {
      title: 'Что насчет well-being (благополучия) сотрудников?',
      variants: [
        'Пока сосредоточимся на главном — развитии компетенций, росте эффективности и вовлеченности сотрудников в рабочие процессы.',
        'Интересное направление. Хотелось бы создать HR-проект, который поможет людям решить не только рабочие проблемы.',
        'Это хорошая история, но в ближайшей перспективе думать об этом пока рано.',
      ],
      correct: 2,
    },
  ];
  const [step,setStep] = React.useState(0)
  let [correct,setCorrect] = React.useState(0)
  let question = questions[step]
const percentage = Math.round((step/questions.length)*100)




  return (
    <div className="App">
{     step !== questions.length ? (
<> <div className='progress'> <div style={{width:`${percentage}%`}} className='progressb'></div> </div>
       <Game question={question} change={(index)=>{setStep(step+1);if(index===1){setCorrect(correct+1)};console.log(correct)}}/></> 
       ):
       (
        <>
        {(() => {
          if(correct<3){
 
            return(<Result
              status='success' 
              title="Вы — HR будущего!"
              subTitle="Вы новатор и готовы немедленно использовать в работе самые интересные и современные инструменты. Опираться при принятии решений лишь на интуицию и по привычке отрабатывать технологии десятилетней давности — это точно не про вас. Возможно, уже через несколько месяцев вы соберете большую команду и решитесь на проект, который заберет главную статуэтку «Премии HR-бренд» в 2020 году.
              "
              extra={<Button onClick={()=>{setStep(step-questions.length);setCorrect(correct=0)}} type="primary">Начать заново</Button>}
              />)} else{
            return(<Result
              status='404'
              title="Вы — HR-искатель!"
              subTitle="Вы открыты новым знаниям и стараетесь быть в курсе всех интересных событий в мире HR. Ищете источники вдохновения в людях, книгах и событиях — причем, не обязательно связанных с работой. Самое главное, помните: иногда полезно отключиться от информационного шума и послушать себя. Наверняка, внутренний голос подскажет, что ваш лучший HR-проект в области обучения персонала или повышения мотивации еще впереди!.
              "
              extra={<Button onClick={()=>{setStep(step-questions.length);setCorrect(correct=0)}} type="primary">Начать заново</Button>}
              />)}
        })()}
    </>
    )}
     
    </div>
  );
}

export default App;
