import React from 'react'
import { Shake } from 'reshake'

function Game({question, change, resultcor}){
    const marks = ['A','B','C']

return(
    <>
  
   <h1> {question.title}</h1>
<ul>
    {question.variants.map((text, index)=>(   <Shake h={1} v={1} r={1.5}>

   <li onClick={()=>change(index)} className='jet' key={text}><div className='jet2'>{marks[index]}</div>{text}</li></Shake>))}
</ul>

</>
);
}
export default Game